import React, { Component } from "react";
import {
  Form,
  Grid,
  Header,
  Segment,
  TransitionablePortal,
  Button
} from "semantic-ui-react";

export default class TransitionablePortalExampleTransition extends Component {
  state = { animation: "fade up", duration: 1200, open: true, qId: 0 };

  Quotes = [
    {
      header: "Thomas Hauser",
      quote:
        'Not voting, is disrespecting the "best" of what our nation stands for.'
    },
    {
      header: "Unknown",
      quote: "The right of election is the very essence of the constitution."
    },
    {
      header: "Patty Berg",
      quote: "People are what any election is all about."
    }
  ];

  handleClick = id => {
    this.setState({ open: !this.state.open, qId: id });
    this.setState({ open: !this.state.open });
  };

  renderPortalContent = () => {
    return (
      <Segment
        style={{
          left: "40%",
          position: "fixed",
          top: "50%",
          zIndex: 1000
        }}
      >
        <Header>{this.Quotes[this.state.qId].header}</Header>
        <p>{this.Quotes[this.state.qId].quote}</p>
      </Segment>
    );
  };

  render() {
    const { animation, duration, open } = this.state;

    return (
      <Grid columns={2}>
        <Grid.Column>
          <Button.Group size="small">
            <Button onClick={() => this.handleClick(0)}>One</Button>
            <Button onClick={() => this.handleClick(1)}>Two</Button>
            <Button onClick={() => this.handleClick(2)}>Three</Button>
          </Button.Group>
          <TransitionablePortal
            open={open}
            transition={{ animation, duration }}
          >
            {this.renderPortalContent()}
          </TransitionablePortal>
        </Grid.Column>
      </Grid>
    );
  }
}
